import React, { useEffect, useState, useRef } from "react";
import nameCard1 from "../../Assets/img/Group 2.svg";
import { gsap } from "gsap";

import "./contact.scss";

let Contact = () => {
    const cardRef = useRef();
    const [cardSw, setCardSw] = useState(false);

    useEffect(() => {
        const clq = document.querySelector(".App");
        clq.style.backgroundColor = "#ffb549";
        console.log("");
    }, []);

    gsap.fromTo(
        cardRef.current,
        {
            opacity: 0,
            x: -100,
        },
        {
            opacity: 1,
            duration: 1,
            x: 0,
        }
    );

    return (
        <>
            <div className="Contact">
                <h1>Click to Contact me</h1>
                <div className="positioner">
                    {cardSw == false ? (
                        <div ref={cardRef} className="group1">
                            <p>Jude Studio</p>
                            <img
                                src={nameCard1}
                                onClick={() => {
                                    setCardSw(!cardSw);
                                }}
                            />
                        </div>
                    ) : (
                        <div ref={cardRef} className="group2">
                            <p>+82 010-3237-0260</p>
                            <p>narag0123@gmail.com</p>
                            <img
                                src={nameCard1}
                                onClick={() => {
                                    setCardSw(!cardSw);
                                }}
                            />
                        </div>
                    )}
                </div>
            </div>
        </>
    );
};

export default Contact;
